.header {
    display: flex;
    align-items: center;
    cursor: pointer;
    color: rgb(216, 216, 216);
    user-select: none;
    background: -webkit-linear-gradient(left, rgba(49, 49, 49, 0.589), rgba(0, 0, 0, 0));
    transition: .1s;
}

.header:hover {
    background: -webkit-linear-gradient(left, rgba(83, 83, 83, 0.685), rgb(20, 20, 20));
}

.headerActive {
    background: -webkit-linear-gradient(left, rgba(83, 83, 83, 0.685), rgb(29, 29, 29));
}

.number {
    margin-right: .5em;
    font-weight: bold;
    text-transform: uppercase;
    background: darkorange;
    text-transform: uppercase;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.wrapper {
    display: flex;
    padding-left: 1em;
    padding-right: 2em;
    text-decoration: none;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    line-break: none;
    cursor: pointer;
}

.wrapper:last-child {
    margin-bottom: .5em;
}

.wrapper:hover {
    background: -webkit-linear-gradient(left, rgba(70, 69, 69, 0.685), rgb(20, 20, 20));
}

.FolderName {
    font-size: 1.1em;
    padding: .1em;
    font-weight: bold;
    text-transform: uppercase;
    background: -webkit-linear-gradient(left, rgb(255, 255, 255), rgb(211, 211, 211));
    ;
    text-transform: uppercase;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    line-break: none;
}

.folderChildren {
    display: flex;
    flex-direction: column;
}

.folderChild {
    color: whitesmoke;
    cursor: pointer;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

/* -----------------------Header Icon----------------------- */
.arrowContainer {
    width: .2em;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: .6em;
    padding-left: .8em;
    padding-right: .8em;
    margin-right: .5em;
    margin-left: 1em;
    position: relative;
    z-index: 99999;
}

.arrowContainer:hover {
    --border: solid white;
}

.arrow {
    border: var(--border);
    border-width: 0 .15em .15em 0;
    padding: .15em;
    margin-left: 1.75em;
    transition: all .1s;
}

.right {
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
}

.down {
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
}