.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    outline: none;
}

.container {
    background-color: rgb(49, 49, 49);
    position: absolute;
    top: 50%;
    left: 50%;
    right: auto;
    bottom: auto;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    width: 30em;
    border-radius: .5em;
    outline: none;
}

.form {
    display: flex;
    flex-direction: column;
    color: whitesmoke;
    padding: 2em;
}

/* Top */

.header {
    font-size: 1.2em;
    width: 100%;
    background-color: rgb(32, 32, 32);
    padding-top: 1em;
    padding-bottom: 1em;
    color: whitesmoke;
}

.headerText {
    padding-left: 1em;
}

/* Input */

.label {
    display: flex;
    flex-direction: column;
}

.inputText {
    background-color: rgb(32, 32, 32);
    font-size: 1em;
    padding: .8em;
    outline: none;
    border: none;
    color: whitesmoke;
}

.inputText:focus {
    background-color: rgba(32, 32, 32, 0.6);
}

/* Mid */

.fileTitle {
    color: whitesmoke;
    margin-top: 1em;
}

.imageUploadContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 9em;
}

.textInfo {
    font-size: .8em;
    font-style: italic;
    text-align: center;
    width: 33em;
}

.imageUpload {
    margin-bottom: 1em;
    width: 4em;
    cursor: pointer;
}

.fileText {
    padding: 1em;
    background-color: rgba(32, 32, 32);
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 1em;
    cursor: pointer;
    min-height: 6.3em;
}

.fileText:hover {
    background-color: rgba(32, 32, 32, 0.6);
}

/* Bottom */

.buttonsContainer {
    width: 100%;
    background-color: rgba(32, 32, 32);
    margin-top: .5em;
}

.close {
    width: 50%;
    height: 3em;
    border: none;
    outline: none;
    background-color: rgba(32, 32, 32);
    color: whitesmoke;
    cursor: pointer;
}

.submit {
    width: 50%;
    height: 3em;
    border: none;
    outline: none;
    background-color: rgba(32, 32, 32, 1);
    color: whitesmoke;
    cursor: pointer;
}

.close:hover,
.submit:hover {
    background-color: rgba(46, 46, 46, 0.6);
}

.disabled {
    user-select: none;
    background-color: rgb(43, 43, 43) !important;
    cursor: default !important;
}