.container {
    width: 100%;
    height: 100vh;
    background-color: #1F1F1F;
    overflow: hidden;
    position: fixed;
    top: 50%;
    left: 50%;
    right: auto;
    bottom: auto;
    margin-right: -50%;
    transform: translate(-50%, -50%);
}

.absolute {
    position: fixed;
    top: 50%;
    left: 50%;
    right: auto;
    bottom: auto;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    color: whitesmoke;
    width: 18em;
    background-color: rgb(20, 20, 20);
    height: 6em;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    padding: 2em;
    border-radius: 1em;
    z-index: 15;
}

.header {
    font-weight: bold;
    margin-bottom: 1em;
}

.fixed {
    position: fixed;
    top: 4em;
    right: 0;
    width: 100%;
    z-index: 11;
    overflow: hidden;
}

.fixedDesign {
    text-align: center;
    margin-top: 0;
    color: rgb(34, 34, 34);
    font-size: 5em;
    font-weight: 700;
    text-shadow: .1em .1em .3em rgb(22, 22, 22);
    user-select: none;
    transition: all .3s;
    overflow: hidden;
}