.bars {
    display: flex;
    flex-direction: column;
}

/* Header */
.header {
    height: 4em;
    width: 100%;
    background-color: black;
    position: fixed;
    z-index: 9;
    display: flex;
    justify-content: center;
    align-items: center;
}

.links {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 35em;
    position: relative;
    column-gap: 2em;
}

.linkContainer {
    display: flex;
    justify-content: center;
    align-items: center;
}

.navLink {
    cursor: pointer;
    text-decoration: none;
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    transition: all .3s;
}

.navLinkActive {
    font-size: 1.4em;
    color: whitesmoke;
    border-bottom: 1px solid rgba(245, 245, 245, 0);
    font-weight: bold;
}

.navLinkActive:hover {
    border-bottom: 1px solid whitesmoke;
}

.navLinkInactive {
    font-size: 1.3em;
    color: #929292;
    border-bottom: 1px solid #92929200;
}

.navLinkInactive:hover {
    color: #b1b1b1;
    border-bottom: 1px solid #b1b1b1;
}

.login {
    cursor: pointer;
    position: absolute;
    right: 0;
    margin-right: 2em;
}

.home {
    cursor: pointer;
    position: absolute;
    left: 0;
    margin-left: 5em;
}

.loginLink {
    font-size: 1.3em;
    color: #929292;
    border-bottom: 1px solid #92929200;
    text-decoration: none;
    border-bottom: 1px solid rgba(245, 245, 245, 0);
    transition: all .3s;
}

.loginLink:hover {
    color: #b1b1b1;
    border-bottom: 1px solid #b1b1b1;
}

/* SideBar */
.sideBar {
    background-color: #141414;
    min-height: 100vh;
    z-index: 10;
    position: fixed;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    transition: all .3s;
    overflow-x: hidden;
}

.sideBarOpen {
    width: 15em;
}

.sideBarClosed {
    width: 4em;
}

.sideBarOpenMain {
    background-color: #141414;
}

.sideBarClosedMain {
    background-color: black;
}

.open {
    opacity: 1;
}

.closed {
    opacity: 0;
}

.navHeader {
    width: 100%;
    height: 4em;
    background-color: black;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: whitesmoke;
    position: relative;
    transition: all .3s;
}

.navText {
    font-size: 1.6em;
    margin-left: 1em;
    font-weight: bolder;
}

.closeContainer {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    transition: all .3s;
}

.buttonClose {
    right: 0;
    width: 4em;
    height: 4em;
    background-color: #141414;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .3s;
    position: absolute;
    color: whitesmoke;
}

.visibleSection {
    transition: all .3s;
}

.closed {
    opacity: 0;
    user-select: none;
    cursor: default !important;
}

.open {
    opacity: 1;
}

.buttonClose:hover {
    background-color: #202020;
    cursor: pointer;
}

.searchBar {
    width: 100%;
    outline: none;
    border: none;
    background-color: red;
    height: 3em;
    background-color: #292929;
    font-size: 1.2em;
    padding-left: 1em;
    color: whitesmoke;
    margin-bottom: 1em;
}

.searchBar:focus {
    background-color: #2e2e2e;
}

.list {
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    width: 100%;
    overflow-x: hidden;
}

.listText {
    color: whitesmoke;
    font-size: 1.4em;
    font-weight: bold;
    margin-left: .3em;
    margin-top: 1em;
}

.listText:first-child {
    margin-top: 0;
}

@media screen and (max-width: 748px) {

    .navLinkActive,
    .navLinkInactive,
    .loginLink {
        font-size: 2vw;
    }
}