.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    outline: none;
}

.modalContainer {
    background-color: rgb(49, 49, 49);
    position: absolute;
    top: 50%;
    left: 50%;
    right: auto;
    bottom: auto;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    width: 30em;
    height: 32em;
    border-radius: .5em;
    outline: none;
}

.form {
    display: flex;
    flex-direction: column;
    color: whitesmoke;
    padding: 2em;
    height: 23em;
    padding-bottom: 0;
}

/* Top */

.header {
    font-size: 1.2em;
    width: 100%;
    background-color: rgb(32, 32, 32);
    padding-top: 1em;
    padding-bottom: 1em;
    color: whitesmoke;
}

.headerText {
    padding-left: 1em;
}

/* Input */

.label {
    display: flex;
    flex-direction: column;
}

.inputText {
    background-color: rgb(32, 32, 32);
    font-size: 1em;
    padding: .8em;
    outline: none;
    border: none;
    color: whitesmoke;
}

.inputText:focus {
    background-color: rgba(32, 32, 32, 0.6);
}

/* Mid */

.fileTitle {
    color: whitesmoke;
    margin-top: 1em;
}

.imageUploadContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 9em;
}

.textInfo {
    font-size: .8em;
    font-style: italic;
    text-align: center;
    width: 33em;
}

.imageUpload {
    margin-bottom: 1em;
    width: 4em;
    cursor: pointer;
}

.fileText {
    padding: 1em;
    background-color: rgba(32, 32, 32);
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 1em;
    cursor: pointer;
}

.fileText:hover {
    background-color: rgba(32, 32, 32, 0.6);
}

/* Bottom */

.buttonsContainer {
    width: 100%;
    background-color: rgba(32, 32, 32);
    margin-top: .5em;
}

.close {
    width: 50%;
    height: 3em;
    border: none;
    outline: none;
    background-color: rgba(32, 32, 32);
    color: whitesmoke;
    cursor: pointer;
}

.submit {
    width: 50%;
    height: 3em;
    border: none;
    outline: none;
    background-color: rgba(32, 32, 32, 1);
    color: whitesmoke;
    cursor: pointer;
}

.close:hover,
.submit:hover {
    background-color: rgba(46, 46, 46, 0.6);
}

.disabled {
    user-select: none;
    background-color: rgb(43, 43, 43) !important;
    cursor: default !important;
}

/* All form and below */
.container {
    position: relative;
    z-index: 2;
    margin-top: .05em;
    width: 97%;
}

.input {
    width: 100%;
    padding-top: .8em;
    padding-bottom: .8em;
    padding-left: .8em;
    background-color: rgba(32, 32, 32);
    border: none;
    outline: none;
    font-size: 1em;
    color: whitesmoke;
    user-select: none;
}

.input:focus {
    background-color: rgb(43, 43, 43);
}

.containerOpen {
    position: relative;
}

.suggestionsContainerOpen {
    width: 103.6%;
    position: absolute;
    background-color: rgb(43, 43, 43);
}

.suggestionsList {
    list-style: none;
    list-style-type: none;
    margin: 0;
    padding: 0;
    height: 15em;
    overflow-y: scroll;
    border: 1px solid black;
    border-bottom: 3px solid black;
    border: none;
    border-top: 1px solid black;
}

.suggestionsList::-webkit-scrollbar-track {
    background-color: rgba(65, 65, 65, 0);
}

.suggestionsList::-webkit-scrollbar {
    width: 1em;
    background-color: rgb(65, 65, 65);
}

.suggestionsList::-webkit-scrollbar-thumb {
    background: rgb(24, 24, 24);
}

.suggestion {
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(42, 42, 42);
    height: 4em;
    cursor: pointer;
}

.suggestionHighlighted {
    background: rgb(55, 55, 55);
}

/* Results */

.resultSelected {
    margin-top: 1.5em;
    font-size: .9em;
}

.results {
    font-size: .8em;
    height: auto;
    max-height: 15em;
    width: 100%;
    margin-top: .1em;
    margin-bottom: 1em;
    overflow-y: auto;
    background: rgb(32, 32, 32);
}

.results::-webkit-scrollbar-track {
    background-color: rgba(65, 65, 65, 0);
}

.results::-webkit-scrollbar {
    width: 1em;
    background-color: rgb(65, 65, 65);
}

.results::-webkit-scrollbar-thumb {
    background: rgb(24, 24, 24);
}

.loadingBox {
    position: absolute;
    width: 100%;
    bottom: 0;
    margin-left: 1em;
    display: flex;
    justify-content: center;
    align-items: center;
}

.imageLoading {
    width: 3em;
    display: flex;
    justify-self: center;
    align-self: center;
    margin-top: 3em;
}

/* Results item */
.resultItem {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 100%;
    padding: .5em;
}

.resultItem:hover {
    background-color: rgb(42, 42, 42);
}

.text {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.itemText {
    width: 70%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    line-break: none;
    font-size: 1.1em;
}

.delete {
    position: absolute;
    top: 0;
    right: 0;
    margin-top: .3em;
    margin-right: .5em;
    font-size: 1.2em;
    cursor: pointer;
    font-style: normal;
    transition: .1s;
    padding-right: .2em;
}

.delete:hover {
    scale: 1.2
}

.number {
    position: absolute;
    top: 0;
    left: 0;
    transition: .1s;
    background: rgb(56, 55, 55);
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    line-break: none;
    font-size: .8em;
    height: 3.3em;
    padding-left: .7em;
    padding-right: .7em;
}

/* Dropdown */
.formDropDown {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: .8em;
    width: 100%;
    padding-left: 1em;
    padding-right: 1em;
}

.formAuthor {
    align-self: flex-end;
    height: 100%;
    font-size: .8em;
    font-style: italic;
    max-width: 8em;
}

.formImage {
    height: 4.5em;
    width: 3.5em;
}

.formTitle {
    max-width: 15em;
}