.header {
    height: 6em;
    padding-top: 1em;
    margin-left: 1.6em;
    margin-right: 2em;
    padding-bottom: .2em;
    background-color: #1F1F1F;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    border-bottom: 1px solid rgba(255, 255, 255, 0.596);
}

.header::after {
    content: '';
}

.path {
    display: flex;
    width: 100%;
}

.addSort {
    display: flex;
    justify-content: space-between;
    margin-top: 1em;
    width: 100%;
}

.pathInfo {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
    overflow: hidden;
    white-space: nowrap;
    width: 50em;
}

.pathSplitter {
    color: #DEDEDE;
    font-size: 1.5em;
    font-weight: bold;
    margin-left: .3em;
    margin-right: .3em;
    user-select: none;
}

.pathItem {
    color: #DEDEDE;
    font-size: 1.5em;
    font-weight: bold;
    cursor: pointer;
    transition: all .3s;
    padding: .4em;
    padding-top: .1em;
    padding-bottom: .1em;
    border-radius: .3em;
    user-select: none;
    text-decoration: none;
}

.pathItem:hover,
.sort:hover {
    background-color: rgb(51, 51, 51);
}

.sort {
    color: #DEDEDE;
    font-size: 1.1em;
    font-weight: bold;
    cursor: pointer;
    transition: all .3s;
    padding: .4em;
    padding-top: .1em;
    padding-bottom: .1em;
    border-radius: .3em;
    user-select: none;
}

@media screen and (max-width: 945px) {
    .header {
        width: 80vw;
    }
}