.container {
    background-color: black;
    min-height: 100vh;
}

/* Welcome */

.welcome {
    padding-top: 8em;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    padding-bottom: 8em;
    width: 93%;
    position: relative;
    border-bottom: 1px solid #c0c0c0;
    background-color: black;
}

.backgroundImage {
    position: absolute;
    top: 0;
    left: -4em;
    width: 100vw;
    height: 100%;
    z-index: 15;
}

.background1 {
    background-image: url(../Assets/gamesbackground.png);
    background-size: cover;
    background-repeat: no-repeat;
    filter: brightness(30%);
}

.background2 {
    background-image: url(../Assets/movies2.png);
    background-size: cover;
    background-repeat: no-repeat;
    filter: brightness(10%);
}

.background3 {
    background-image: url(../Assets/games2.png);
    background-size: cover;
    background-repeat: no-repeat;
    filter: brightness(10%);
}

.background4 {
    background-image: url(../Assets/books2.png);
    background-size: cover;
    background-repeat: no-repeat;
    filter: brightness(10%);
}

.welcomePreHeader {
    color: #878a8f;
    text-transform: uppercase;
    font-size: 1em;
    letter-spacing: .3em;
    margin-top: 1em;
    margin-bottom: 1em;
    z-index: 210;
}

.welcomeHeader {
    line-height: 1.1em;
    font-size: 2.8em;
    font-weight: 700;
    letter-spacing: -.05em;
    max-width: 16em;
    z-index: 210;
    color: rgb(224, 224, 224)
}

.welcomeText {
    font-size: 1.4em;
    line-height: 1.3em;
    color: #878a8f;
    letter-spacing: normal;
    margin-top: 1.5em;
    max-width: 25em;
    z-index: 210;
}

/* Movies */
.movies {
    padding-top: 6em;
    padding-bottom: 6em;
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    border-bottom: 1px solid #c0c0c0;
    width: 93%;
    position: relative;
    background-color: black;
}

.image {
    width: 30%;
    height: 100%;
    box-shadow: 0 0 1em rgb(0, 0, 0), 0 0 1em .2em black;
    margin: 2em;
    z-index: 210;
}

.movieContainer {
    display: flex;
    flex-direction: column;
    text-align: left;
    z-index: 210;
}

.movieHeader {
    line-height: 1.1em;
    font-size: 2.8em;
    font-weight: 700;
    letter-spacing: -.05em;
    color: rgb(224, 224, 224);
    z-index: 210;
}

.movieText {
    font-size: 1.4em;
    line-height: 1.3em;
    color: #878a8f;
    letter-spacing: normal;
    margin-top: 1.5em;
    max-width: 30em;
    z-index: 210;
}

/* games */

.games {
    padding-top: 6em;
    padding-bottom: 6em;
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid #c0c0c0;
    width: 93%;
    justify-content: center;
    position: relative;
    background-color: black;
}

.gameContainer {
    display: flex;
    flex-direction: column;
    text-align: left;
    z-index: 210;
}

/* Credits */

.credits {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 6em;
    padding-bottom: 6em;
    width: 93%;
    position: relative;
}

.creditsBackground {
    position: absolute;
    top: 0;
    left: -4em;
    width: 100vw;
    height: 100%;
    z-index: 15;
    background-color: black;
}

.logoContainer {
    display: flex;
    z-index: 20;
}

.logo {
    height: 3em;
    filter: drop-shadow(0 0 1em rgb(71, 71, 71));
    margin: 2em;
    z-index: 20;
    background-color: whitesmoke;
    padding: .5em;
    border-radius: 1em;
    background-color: #2e2e2e;
}

@media screen and (max-width: 850px) {

    .welcome,
    .movies,
    .games {
        padding-top: 16vw;
        padding-bottom: 16vw;
        width: 80vw;
    }

    .credits {
        padding-top: 16vw;
        padding-bottom: 16vw;
    }

    .logo {
        height: 6vw;
        margin: 4vw;
        padding: 1vw;
        border-radius: 2vw;
    }

    .welcomePreHeader {
        font-size: 3vw;
        letter-spacing: .9vw;
        margin-top: 3vw;
        margin-bottom: 3vw;
    }

    .welcomeHeader,
    .movieHeader {
        line-height: 5.3vw;
        font-size: 5vw;
        letter-spacing: -.15vw;
        max-width: 48vw;
    }

    .welcomeText,
    .movieText {
        font-size: 3.2vw;
        line-height: 3.9vw;
        margin-top: 4.5vw;
        max-width: 75vw;
    }

    .image {
        width: 40%;
        height: 100%;
        margin: 2em;
    }
}