.gameItemContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transition: all .3s;
    cursor: pointer;
    text-decoration: none;
    position: relative;
}

.gameItemContainer:hover {
    scale: 1.1;
}

.gamePoster {
    height: 9em;
}

.imageContainer {
    width: 19.3em;
    height: 9em;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    background-color: rgb(19, 18, 18);
}

.homePoster {
    height: 9em;
    filter: brightness(50%)
}

.gameFooterContainer {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: column;
    height: 2.7em;
    margin-top: .2em;
    width: 100%;
}

.gameFooterHeader {
    color: #DEDEDE;
    width: 10.5em;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.gameFooterYear {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: #6E6E6E;
    width: 10.5em;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    display: flex;
    justify-content: space-between;
}

.deleteContainer {
    position: absolute;
    top: 0;
    right: 0;
    color: #DEDEDE;
    font-size: 1.1em;
    font-weight: bold;
    padding-right: .5em;
    padding-left: .5em;
    padding-top: .1em;
    padding-bottom: .1em;
    border-radius: 10em;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .3s;
}

.deleteContainer:hover {
    color: whitesmoke;
    background-color: rgba(116, 116, 116, 0.685);
}

.disabled {
    opacity: 1;
    filter: brightness(0);
    width: 100%;
}